import React, {Component} from 'react';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'

class Galaxy extends Component {

  clock = new THREE.Clock()

  geometry = null
  material = null
  points = null
  parameters = {}
  sizes = {}

  componentDidMount () {
    this.sizes.width = this.mount.clientWidth;
    this.sizes.height = window.innerHeight; //this.mount.clientHeight; //window.innerHeight; //;
   
    this.parameters.count = 200000
    this.parameters.size = 0.005
    this.parameters.radius = 5
    this.parameters.branches = 3
    this.parameters.spin = 1
    this.parameters.randomness = 0.5
    this.parameters.randomnessPower = 3
    this.parameters.insideColor = '#ff6030'
    this.parameters.outsideColor = '#074aff'
        
    //ADD SCENE
    this.scene = new THREE.Scene ();
    //ADD CAMERA
    this.camera = new THREE.PerspectiveCamera (75, this.sizes.width / this.sizes.height, 0.1, 100);
    this.camera.position.x = 0.5;
    this.camera.position.y = 1.0;
    this.camera.position.z = 2.0;

    const listener = new THREE.AudioListener();
    this.camera.add( listener );

    // create a global audio source
    const sound = new THREE.Audio( listener );

    // load a sound and set it as the Audio object's buffer
    const audioLoader = new THREE.AudioLoader();
    audioLoader.load( './cosmic.mp3', function( buffer ) {
      sound.setBuffer( buffer );
      sound.setLoop( true );
      sound.setVolume( 0.3 );
      sound.play();
    });

    //ADD RENDERER
    this.renderer = new THREE.WebGLRenderer ({antialias: true});
    //this.renderer.setClearColor ('#000000');
    this.renderer.setSize (this.sizes.width, this.sizes.height);
    this.renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
    this.mount.appendChild (this.renderer.domElement);

    // Controls
    const controls = new OrbitControls(this.camera, this.renderer.domElement)
    controls.enableDamping = true
    controls.maxDistance = 5;
    controls.rotateSpeed = 0.05;
    controls.zoomSpeed = 0.05;
    controls.enablePan = false;
    
    //ADD CUBE
    //const geometry = new THREE.BoxGeometry (1, 1, 1);
    //const material = new THREE.MeshBasicMaterial ({color: '#433F81'});
    //this.cube = new THREE.Mesh (geometry, material);
    //this.scene.add (this.cube);
    //this.start ();
    
  window.addEventListener('resize', () =>
  {
      // Update sizes
      this.sizes.width = window.innerWidth
      this.sizes.height = window.innerHeight
  
      // Update camera
      this.camera.aspect = this.sizes.width / this.sizes.height
      this.camera.updateProjectionMatrix()
  
      // Update renderer
      this.renderer.setSize(this.sizes.width, this.sizes.height)
      this.renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
  })

    this.generateGalaxy(this.parameters, this.points, this.scene, this.renderer)
  }

  generateGalaxy = (parameters, points, scene, renderer) =>
  {
      if(this.points !== null)
      {
          this.geometry.dispose()
          this.material.dispose()
          this.scene.remove(this.points)
      }
  
      /**
       * Geometry
       */
      this.geometry = new THREE.BufferGeometry()
  
      const positions = new Float32Array(parameters.count * 3)
      const colors = new Float32Array(parameters.count * 3)
      const scales = new Float32Array(parameters.count * 1)
      const radomness = new Float32Array(parameters.count * 3)
  
      const insideColor = new THREE.Color(parameters.insideColor)
      const outsideColor = new THREE.Color(parameters.outsideColor)
  
      for(let i = 0; i < parameters.count; i++)
      {
          const i3 = i * 3
  
          // Position
          const radius = Math.random() * parameters.radius
  
          const branchAngle = (i % parameters.branches) / parameters.branches * Math.PI * 2
  
          positions[i3 + 0] = Math.cos(branchAngle) * radius
          positions[i3 + 1] = 0.0
          positions[i3 + 2] = Math.sin(branchAngle) * radius
  
          //Radomness
          const randomX = Math.pow(Math.random(), parameters.randomnessPower) * (Math.random() < 0.5 ? 1 : - 1) * parameters.randomness * radius
          const randomY = Math.pow(Math.random(), parameters.randomnessPower) * (Math.random() < 0.5 ? 1 : - 1) * parameters.randomness * radius
          const randomZ = Math.pow(Math.random(), parameters.randomnessPower) * (Math.random() < 0.5 ? 1 : - 1) * parameters.randomness * radius
  
          radomness[i3 + 0] = randomX
          radomness[i3 + 1] = randomY
          radomness[i3 + 2] = randomZ
  
          // Color
          const mixedColor = insideColor.clone()
          mixedColor.lerp(outsideColor, radius / parameters.radius)
  
          colors[i3    ] = mixedColor.r
          colors[i3 + 1] = mixedColor.g
          colors[i3 + 2] = mixedColor.b
  
          //Scale
          scales[i] = Math.random()
  
      }
  
      this.geometry.setAttribute('position', new THREE.BufferAttribute(positions, 3))
      this.geometry.setAttribute('color', new THREE.BufferAttribute(colors, 3))
      this.geometry.setAttribute('aScale', new THREE.BufferAttribute(colors, 1))
      this.geometry.setAttribute('aRadomness', new THREE.BufferAttribute(radomness, 3))
  
      /**
       * Material
       */
      this.material = new THREE.ShaderMaterial({
          depthWrite: false,
          blending: THREE.AdditiveBlending,
          vertexColors: true,
          vertexShader: `
            uniform float uSize;
            attribute float aScale;
            varying vec3 vColor;
            uniform float uTime;
            attribute vec3 aRadomness;

            void main() {
                vec4 modelPosition = modelMatrix * vec4(position, 1.0);

                //Spin
                float angle = atan(modelPosition.x, modelPosition.z);
                float distanceOfParticleFromCenter = length(modelPosition.xz);
                float angleOffset = (1.0 / distanceOfParticleFromCenter) * uTime * 0.05;
                angle += angleOffset;
                modelPosition.x = distanceOfParticleFromCenter * cos(angle);
                modelPosition.z = distanceOfParticleFromCenter * sin(angle);

                //Randonmess
                //modelPosition.x += aRadomness.x;
                //modelPosition.y += aRadomness.y;
                //modelPosition.z += aRadomness.z;
                modelPosition.xyz += aRadomness;

                vec4 viewPosition = viewMatrix * modelPosition;
                vec4 projectionPosition = projectionMatrix * viewPosition;
                gl_Position = projectionPosition;

                gl_PointSize = uSize * aScale;
                gl_PointSize *= ( 1.0 / - viewPosition.z );

                vColor = color;
            }
          `,
          fragmentShader: `
            varying vec3 vColor;

            void main() {

                //Disc
                //float strenght = distance(gl_PointCoord, vec2(0.5));
                //strenght = 1.0 - step(0.5, strenght);

                //Diffuse Point
                //float strenght = distance(gl_PointCoord, vec2(0.5));
                //strenght *= 2.0;
                //strenght = 1.0 - strenght;

                //light Point
                float strenght = distance(gl_PointCoord, vec2(0.5));
                strenght = 1.0 - strenght;
                strenght = pow(strenght, 5.0);

                //Final Color
                vec3 color = mix(vec3(0.0), vColor, strenght);


                gl_FragColor = vec4(color, 1.0);
            }
          `,
          uniforms: {
              uTime: { value: 0 },
              uSize: { value: 10.0 * renderer.getPixelRatio()}
          }
      })
  
      /**
       * Points
       */
      points = new THREE.Points(this.geometry, this.material)
      scene.add(points)
      this.start()
  }

  componentWillUnmount () {
    this.stop ();
    this.mount.removeChild (this.renderer.domElement);
  }
  start = () => {
    if (!this.frameId) {
      this.frameId = requestAnimationFrame (this.animate);
    }
  };
  stop = () => {
    cancelAnimationFrame (this.frameId);
  };
  animate = () => {
    //this.cube.rotation.x += 0.01;
    //this.cube.rotation.y += 0.01;
    const elapsedTime = this.clock.getElapsedTime()
    // Update Material
    if(this.material){
      this.material.uniforms.uTime.value = elapsedTime;
      if(this.points !== null)
      {
          this.geometry.dispose()
          this.material.dispose()
          this.scene.remove(this.points)
      }
    }

    this.renderScene ();
    this.frameId = window.requestAnimationFrame (this.animate);
  };
  renderScene = () => {
    this.renderer.render (this.scene, this.camera);
  };

  render () {
    return (
      <div
        style={{width: this.innerWidth, height: '400px'}}
        ref={mount => {
          this.mount = mount;
        }}
      />
    );
  }
}
export default Galaxy;
